.pagination {
  text-align: center;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.pagination button {
  width: 38px;
  height: 38px;
  cursor: pointer;
  border: 1px solid #111;
  border-radius: 50px;
  padding: 5px 10px;
}

.btn-first {
  color: #b4afaf;
  background-color: #111;
  margin-left: 5px;
}

.btn-last {
  background-color: #eac645;
  margin-right: 5px;
}

.numbers {
  background-color: #fff;
  gap: 10px;
  margin-left: 14px;
  margin-right: 14px;
  display: flex;
}

.pagination button.active {
  color: #fff;
  background-color: #111;
}

@media screen and (min-width: 768px) {
  .pagination {
    max-width: 768px;
    margin-top: 40px;
  }

  .pagination button {
    width: 44px;
    height: 44px;
  }

  .btn-first {
    margin-left: 8px;
  }

  .btn-last {
    margin-right: 8px;
  }

  .numbers {
    margin-left: 24px;
    margin-right: 24px;
  }
}

@media screen and (min-width: 1440px) {
  .pagination {
    max-width: 1440px;
  }
}

